import { html } from 'lit';
import { ifDefined } from 'lit-html/directives/if-defined.js';

export type TMarketColorSwatchTemplateArgs = Partial<HTMLMarketColorSwatchElement> & {
  label?: string;
};

export const ColorSwatchTemplate = ({ disabled, name, selected, value }: TMarketColorSwatchTemplateArgs) => html`
  <market-color-swatch
    ?disabled=${disabled}
    name=${ifDefined(name)}
    ?selected=${selected}
    value=${ifDefined(value)}
  ></market-color-swatch>
`;
